import React, { memo, useEffect, useState } from "react";
import Header from "../../partials/Header";
import SideBar from "../Partials/SideBar";
import { NavLink } from "react-router-dom";
import "react-perfect-scrollbar/dist/css/styles.css";
import $ from "jquery";
import { isSameDay } from "date-fns";
import { enGB } from "date-fns/locale";
import { Calendar } from "react-nice-dates";
import "react-nice-dates/build/style.css";
import M from "materialize-css";
import { format } from "date-fns";
import DateFnsUtils from "@date-io/date-fns";
import Grid from "@material-ui/core/Grid";
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, { Pagination, Thumbs } from "swiper";
import "swiper/swiper-bundle.css";
import {useDispatch , useSelector} from 'react-redux';
import {AddScheduleAction} from './../../../../entity/Redux/Doctors/Action';

import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from "@material-ui/pickers";

SwiperCore.use([Pagination, Thumbs]);
export default memo((props) => {
  const userId = props.userId

  const [paginateThumbs, setPaginateThumbs] = useState();
  const dispatch = useDispatch();
  const pagin = [];
  for (let i = 0; i < 30; i += 1) {
    pagin.push(
      <SwiperSlide style={{ listStyle: "none" }} key={`slide-${i}`} tag="li">
        {({ isActive }) => (
          <span id="pagin-item" className={`${isActive ? "active" : ""}`}>
            {i + 1}
          </span>
        )}
      </SwiperSlide>
    );
  }

  $("span.nice-dates-day").click(function (){
    $("span.nice-dates-day").removeClass("-selected")
    $(this).addClass("-selected");
  })

  const thumbs = [];
  for (let i = 0; i < 30; i += 1) {
    pagin.push(
      <SwiperSlide key={`thumb-${i}`} tag="li" style={{ listStyle: "none" }}>
        <span id="pagin-item">{i + 1}</span>
      </SwiperSlide>
    );
  }

  const [selectedDates, setSelectedDates] = React.useState([
    new Date("2014-08-18T21:11:54"),
  ]);
  const [oldSelectedDates, setOldSelectedDates] = React.useState([]);
  const [selectTime, setSelectTime] = useState({});
  const [description, setDescription] = useState({});

  useEffect(() => {
    $("#loader-wrapper").fadeOut();
    $("#show_schedule.calender").click(function () {
      $(this).toggleClass("active");
      if ($(this).hasClass("active")) {
        $(this).html("");
        $(this).append('<i class="material-icons">close</i>');
        $(".back_calender").fadeIn();
      } else {
        $(this).html("");
        $(this).append('<i class="material-icons">date_range</i>');
        $(".back_calender").fadeOut();
      }
    });

    var time = document.querySelectorAll(".timepicker");
    M.Timepicker.init(time);

    var elems = document.querySelectorAll(".tabs");
    M.Tabs.init(elems);

    var datePicker = document.querySelectorAll(".datepicker");
    M.Datepicker.init(datePicker);

    document.addEventListener("DOMContentLoaded", function () {
      var elems = document.querySelectorAll("select");
      M.FormSelect.init(elems);
    });

    $("ul.scheduleUl li").click(function () {
      $("ul.scheduleUl li").removeClass("active");
      $(this).toggleClass("active");
    });
  });

  const handleTimeStart = (date) => {
    let time = new Date(date).getTime();
    setSelectTime({
      ...selectTime,
      startTime: time,
    });
  };

  const handleTimeEnd = (date) => {
    // date.setTimeZone(timeZone.getTimeZone("UTC"));
    let time = new Date(date).getTime();
    setSelectTime({
      ...selectTime,
      endTime: time,
    });
  };

  const modifiers = {
    selected: (date) =>
      selectedDates.some((selectedDate) => isSameDay(selectedDate, date)),
  };

  const modifiers_old = {
    selected: (date) =>
      oldSelectedDates.some((oldsel) => isSameDay(oldsel, date)),
  };

  const handleInput = (e) => {
    setDescription({
      ...description,
      description: e.target.value,
    });
  };
  const handleDayClick = (date) => {
  
    const newDate = date && format(date, "dd MMM yyyy", { locale: enGB });
    setSelectedDates([newDate]);
  };
  const handleOldClick = (date) => {
    console.log("date selected : ", date);
  };

  const submitForm = (e) => {
    e.preventDefault();
    let id = $("ul.scheduleUl li.active").attr("id");
    let finalData = {...selectTime};
    finalData.description = description.description ? description.description : '';
    finalData.connectionType = id;
    finalData.patient = userId;
    let dateTime = new Date(selectedDates).getTime();
    finalData.date = dateTime;
    if (finalData.description == "" || !finalData.description ||
        finalData.endTime == "" || !finalData.endTime ||
        finalData.startTime == "" || !finalData.startTime ||
        finalData.date == "" || !finalData.date)
    {
      M.toast({html : "Please enter all data ..."})
    }else{
      // console.log("date : " , finalData.endTime ? "ok" :"no");
      dispatch(AddScheduleAction(finalData));

    }

  };
  return (
    <div>
      <div id="loader-wrapper">
        <div id="loader"></div>
        <div className="loader-section section-left"></div>
        <div className="loader-section section-right"></div>
      </div>

      <Header />
      <div id="main">
        <div className="wrapper">
          <SideBar />
          <div id="back_shado"></div>
          <div className="container">
            <div className="row">
              <div className="col s12" style={{ marginBottom: 30 }}>
                <div className="col s12">
                  <div id="breadcrumbs-wrapper">
                    <div className="container">
                      <div className="row">
                        <div className="col s10 m6 l6">
                          <h5 className="breadcrumbs-title">Schedule Add</h5>
                          <ol className="breadcrumbs">
                            <li>
                              <NavLink to="/doctor">
                                <a>Dashboard</a>
                              </NavLink>
                            </li>

                            <li>
                              <a className="active">schedule</a>
                            </li>
                          </ol>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                <div className="col l6 m6 s12" style={{ padding: "15px" }}>
                  <div
                    className={"col s12"}
                    style={{
                      padding: 0,
                      backgroundColor: "#222222",
                      borderRadius: 10,
                    }}
                  >
                    <ul
                      id="projects-collection"
                      className="collection z-depth-1"
                    >
                      <li
                        className="collection-item avatar"
                        id={"schedule_title"}
                      >
                        <i className="material-icons" id="custom_icon">
                          add
                        </i>
                        <h6 className="collection-header m-0">Add</h6>
                        <p>Add new schedule</p>
                        <span id="add_schedule">
                          <i className="material-icons">done</i>
                        </span>
                      </li>

                      <div
                        style={{
                          width: "100%",
                          height: "20px",
                          float: "right",
                        }}
                      ></div>

                      <ul className="scheduleUl">
                        <li className="active" id="1">
                          <i className={"material-icons"}>voice_chat</i>
                        </li>

                        <li id="2">
                          <i className={"material-icons"}>settings_voice</i>
                        </li>

                        <li id="3">
                          <i className={"material-icons"}>chat</i>
                        </li>
                      </ul>

                      <div id="test-swipe-1 active" className="col s12">
                        <div className="input-field col s12">
                          <p>Description for Schedule *</p>
                          <div id="input_back">
                            <input
                              className="validate-form"
                              type="text"
                              onChange={(e) => handleInput(e)}
                              autoComplete="false"
                              placeholder="write description for your schedule ..."
                              name="description"
                            />
                          </div>
                        </div>

                        <div className={"col s12 l6"}>
                          <MuiPickersUtilsProvider utils={DateFnsUtils}>
                            <Grid container justify="space-around">
                              <KeyboardTimePicker
                                margin="normal"
                                id="time-picker"
                                label="Start time"
                                value={selectTime.startTime}
                                onChange={handleTimeStart}
                                KeyboardButtonProps={{
                                  "aria-label": "change time",
                                }}
                              />
                            </Grid>
                          </MuiPickersUtilsProvider>
                        </div>

                        <div className={"col s12 l6"}>
                          <MuiPickersUtilsProvider utils={DateFnsUtils}>
                            <Grid container justify="space-around">
                              <KeyboardTimePicker
                                margin="normal"
                                id="time-picker"
                                label="End time"
                                value={selectTime.endTime}
                                onChange={handleTimeEnd}
                                KeyboardButtonProps={{
                                  "aria-label": "change time",
                                }}
                              />
                            </Grid>
                          </MuiPickersUtilsProvider>
                        </div>

                        <div
                          className={"col s12"}
                          style={{
                            paddingLeft: 0,
                            paddingRight: 0,
                            paddingBottom: 15,
                          }}
                        >
                          <Calendar
                            onDayClick={handleDayClick}
                            modifiers={modifiers}
                            locale={enGB}
                          />
                        </div>

                        <div
                          className="col s12 l6"
                          style={{ marginBottom: 15 }}
                        >
                          <a
                            onClick={(e) => submitForm(e)}
                            className="btn waves-effect blue darken-4"
                            style={{ textTransform: "unset" }}
                          >
                            Add Schedule
                          </a>
                        </div>
                      </div>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div id="back_detail">
        <div className="container">
          <div className="row">
            <div className="col s12">
              <div className="col s1 m2 l4"></div>
              <div className="col s10 m8 l4" style={{ marginTop: 30 }}>
                <div className="card grey darken-4">
                  <div className="card-content white-text">
                    <span className="card-title">Delete Package</span>
                    <p>
                      Do you want to delete the <span id="serial_pp"></span>{" "}
                      package?
                    </p>
                  </div>
                  <div className="card-action">
                    <a
                      className="btn btn-secondary waves-effect red"
                      id="deleted"
                      style={{
                        textTransform: "lowercase",
                        marginBottom: 15,
                        float: "left",
                      }}
                    >
                      delete
                    </a>
                    <a
                      className="btn btn-secondary waves-effect blue darken-3"
                      id="cancel"
                      style={{
                        textTransform: "lowercase",
                        marginBottom: 15,
                        float: "right",
                      }}
                    >
                      back
                    </a>
                  </div>
                </div>
              </div>
              <div className="col s1 m2 l4"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
});
